body {
  min-width: 320px;
  font-family: 'Montserrat', sans-serif;
  line-height: 140%;
  font-size: 16px;
  font-weight: 400;
  color: #434242;
}
h1 {
  font-family: 'Oswald', sans-serif;
  line-height: 130%;
  font-size: 54px;
  font-weight: 700;
  color: #fff;
  letter-spacing: 0.02px;
}
@media (max-width: 767px) {
  h1 {
    line-height: 120%;
    font-size: 44px;
  }
}
h2 {
  font-family: 'Oswald', sans-serif;
  line-height: 130%;
  font-size: 46px;
  font-weight: 700;
  color: #000;
  letter-spacing: 0.02px;
}
@media (max-width: 767px) {
  h2 {
    font-size: 40px;
  }
}
h4 {
  line-height: 130%;
  font-size: 18px;
  font-weight: 600;
  color: #000;
}
img {
  width: 100%;
  display: block;
}
.box-center {
  margin: 0 auto;
  padding: 0 60px;
  max-width: 1320px;
  width: 100%;
  box-sizing: border-box;
}
@media (max-width: 1023px) {
  .box-center {
    padding: 0 40px;
  }
}
@media (max-width: 767px) {
  .box-center {
    padding: 0 20px;
  }
}
.section-full {
  width: 100%;
  position: relative;
  overflow: hidden;
}
.section-full__list-item {
  margin-top: 15px;
  padding-left: 12px;
  position: relative;
}
.section-full__list-item:before {
  content: '';
  width: 6px;
  height: 6px;
  position: absolute;
  top: 7px;
  left: 0;
  border-radius: 50%;
  background-color: #961914;
}
.section-full__list-item:first-child {
  margin-top: 0;
}
.show {
  opacity: 1 !important;
  visibility: visible !important;
}
.show .popup__dialog {
  opacity: 1;
  transform: translateY(0);
  transition: opacity 11800ms ease, transform 11800ms ease;
}
.swiper-pagination-bullet {
  width: 8px;
  height: 8px;
  opacity: 1;
  box-sizing: border-box;
  cursor: pointer;
}
.swiper-pagination-bullet-active {
  background: #961914 !important;
}
.section-full__start .btn {
  opacity: 0;
  transform: translateY(30px);
  transition: opacity 800ms 1400ms ease, transform 800ms 1400ms ease;
}
.section-full__start h1 {
  opacity: 0;
  transform: translateY(30px);
  transition: opacity 800ms 400ms ease, transform 800ms 400ms ease;
}
.section-full__start-desc {
  opacity: 0;
  transform: translateY(30px);
  transition: opacity 800ms 800ms ease, transform 800ms 800ms ease;
}
.section-full__start.js-init-animation .btn {
  opacity: 1;
  transform: translateY(0);
}
.section-full__start.js-init-animation h1 {
  opacity: 1;
  transform: translateY(0);
}
.section-full__start.js-init-animation path {
  stroke-dasharray: 625;
  stroke-dashoffset: 625;
  animation: draw 800ms linear forwards;
  animation-delay: 1400ms;
  animation-iteration-count: 1;
}
.section-full__start.js-init-animation .section-full__start-desc {
  opacity: 1;
  transform: translateY(0);
}
.section-full__barf .section-full__column.js-scroll {
  opacity: 0;
  transition: opacity 800ms ease;
}
.section-full__barf .section-full__column.js-init-animation {
  opacity: 1;
}
.section-full__barf h2 {
  opacity: 0;
  transition: opacity 800ms ease;
}
.section-full__barf h2.js-init-animation {
  opacity: 1;
}
.section-full__barf .section-full__column-desk {
  opacity: 0;
  transition: opacity 800ms ease;
}
.section-full__barf .section-full__column-desk.js-init-animation {
  opacity: 1;
}
.section-full__barf h4 {
  opacity: 0;
  transition: opacity 800ms ease;
}
.section-full__barf h4.js-init-animation {
  opacity: 1;
}
.section-full__barf .section-full__list .section-full__list-item {
  opacity: 0;
  transition: opacity 800ms ease;
}
.section-full__barf .section-full__list.js-init-animation .section-full__list-item {
  opacity: 1;
}
.section-full__food-header h2 {
  opacity: 0;
  transform: translateY(-30px);
  transition: opacity 800ms ease, transform 800ms ease;
}
.section-full__food-header .section-full__food-desc {
  opacity: 0;
  transform: translateY(30px);
  transition: opacity 800ms ease, transform 800ms ease;
}
.section-full__food-header.js-init-animation h2,
.section-full__food-header.js-init-animation .section-full__food-desc {
  opacity: 1;
  transform: translateY(0);
}
.section-full__food .food-list .food-list__item .food-list__item-img {
  opacity: 0;
  transform: translateY(-100px);
  transition: opacity 800ms ease, transform 800ms ease;
}
.section-full__food .food-list .food-list__item .food-list__item-text {
  opacity: 0;
  transition: opacity 800ms 1600ms ease;
}
.section-full__food .food-list .food-list__item:nth-child(2) .food-list__item-img {
  transition-delay: 400ms;
}
.section-full__food .food-list .food-list__item:nth-child(3) .food-list__item-img {
  transition-delay: 800ms;
}
.section-full__food .food-list .food-list__item:last-child .food-list__item-img {
  transition-delay: 1200ms;
}
.section-full__food .food-list.js-init-animation .food-list__item-img {
  opacity: 1;
  transform: translateY(0);
}
.section-full__food .food-list.js-init-animation .food-list__item-text {
  opacity: 1;
}
.section-full__whyzoo h2 {
  opacity: 0;
  transform: translateX(140px);
  transition: opacity 800ms ease, transform 800ms ease;
}
.section-full__whyzoo .step-list__item {
  opacity: 0;
  transition: opacity 800ms 800ms ease;
}
.section-full__whyzoo.js-init-animation h2 {
  opacity: 1;
  transform: translateX(0);
}
.section-full__whyzoo.js-init-animation .step-list__item {
  opacity: 1;
}
.section-full__getzoo h2 {
  opacity: 0;
  transform: translateX(140px);
  transition: opacity 800ms ease, transform 800ms ease;
}
.section-full__getzoo .step-list__item {
  opacity: 0;
  transition: opacity 800ms 800ms ease;
}
.section-full__getzoo .step-list__item:before,
.section-full__getzoo .step-list__item:after {
  opacity: 0;
  transition: opacity 200ms 2400ms ease;
}
.section-full__getzoo .step-list__item:nth-child(2) {
  transition-delay: 1200ms;
}
.section-full__getzoo .step-list__item:nth-child(3) {
  transition-delay: 1600ms;
}
.section-full__getzoo .step-list__item:last-child {
  transition-delay: 2000ms;
}
.section-full__getzoo.js-init-animation h2 {
  opacity: 1;
  transform: translateX(0);
}
.section-full__getzoo.js-init-animation .step-list__item {
  opacity: 1;
}
.section-full__getzoo.js-init-animation .step-list__item:before,
.section-full__getzoo.js-init-animation .step-list__item:after {
  opacity: 1;
}
.section-full__contact .section-full__column {
  opacity: 0;
  transform: translateY(150px);
  transition: opacity 800ms ease, transform 800ms ease;
}
.section-full__contact .section-full__column.js-init-animation {
  opacity: 1;
  transform: translateY(0);
}
.section-full__contact .section-full__column.js-init-animation:last-child {
  transition-delay: 800ms;
}
@media (max-width: 767px) {
  .section-full__contact .section-full__column.js-init-animation:last-child {
    transition-delay: 0;
  }
}
@-moz-keyframes draw {
  100% {
    stroke-dashoffset: 0;
  }
}
@-webkit-keyframes draw {
  100% {
    stroke-dashoffset: 0;
  }
}
@-o-keyframes draw {
  100% {
    stroke-dashoffset: 0;
  }
}
@keyframes draw {
  100% {
    stroke-dashoffset: 0;
  }
}
.btn {
  padding: 1px 0 0;
  width: 226px;
  height: 48px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  text-align: center;
  line-height: 140%;
  color: #fff;
  font-weight: 600;
  text-decoration: none;
  border-radius: 8px;
  border: 0;
  box-sizing: border-box;
  transition: all 300ms ease;
  cursor: pointer;
}
.btn--red {
  background-color: #961914;
}
.btn--red:hover {
  background-color: #75110d;
}
.btn--white {
  color: #961914;
  background-color: #fff;
  border: 1px solid #961914;
}
.btn--white:hover {
  background-color: #ead1d0;
}
.field {
  padding: 22px 12px 6px;
  width: 100%;
  height: 100%;
  display: block;
  font-family: 'Montserrat', sans-serif;
  line-height: 140%;
  font-size: 14px;
  color: #fff;
  border: 0;
  background: none;
  box-sizing: border-box;
}
.field__label {
  margin-top: -10px;
  display: block;
  position: absolute;
  left: 12px;
  top: 50%;
  line-height: 20px;
  font-size: 14px;
  color: #fff;
  opacity: 0.6;
  transition: all 300ms ease;
}
.field:focus + label,
.field:not(:placeholder-shown) + label {
  margin: 0;
  top: 4px;
  font-size: 12px;
}
.field__checkbox {
  position: absolute;
  top: 0;
  left: 0;
  appearance: none;
}
.field__checkbox:checked + label:after {
  opacity: 1;
}
.field__checkbox-label {
  padding-left: 24px;
  display: block;
  line-height: 140%;
  font-size: 12px;
  position: relative;
  cursor: pointer;
}
.field__checkbox-label:before,
.field__checkbox-label:after {
  content: '';
  position: absolute;
}
.field__checkbox-label:before {
  width: 16px;
  height: 16px;
  top: 0;
  left: 0;
  border-radius: 2px;
  background-color: #fff;
  box-sizing: border-box;
}
.field__checkbox-label:after {
  width: 10px;
  height: 8px;
  top: 4px;
  left: 3px;
  background: url("../img/svg/icn-check.svg") no-repeat center;
  opacity: 0;
}
.field-line__radio {
  margin-bottom: 12px;
  width: 100%;
  position: relative;
}
.field-line__radio label {
  padding-left: 24px;
  display: block;
  line-height: 140%;
  font-size: 14px;
  white-space: nowrap;
  position: relative;
  cursor: pointer;
}
.field-line__radio label:before,
.field-line__radio label:after {
  content: '';
  position: absolute;
}
.field-line__radio label:before {
  margin-top: -1px;
  width: 16px;
  height: 16px;
  top: 50%;
  left: 0;
  border-radius: 50%;
  background-color: #fff;
  border: 1px solid #434242;
  transform: translateY(-50%);
  box-sizing: border-box;
}
.field-line__radio label:after {
  margin-top: -1px;
  width: 10px;
  height: 10px;
  top: 50%;
  left: 3px;
  opacity: 0;
  border-radius: 50%;
  background-color: #961914;
  transform: translateY(-50%);
}
.field__radio {
  position: absolute;
  top: 0;
  left: 0;
  appearance: none;
}
.field__radio:checked + label:before {
  border: 1px solid #961914;
}
.field__radio:checked + label:after {
  opacity: 1;
}
.field__text {
  padding: 8px 15px;
  width: 100%;
  height: 122px;
  font-family: 'Montserrat', sans-serif;
  line-height: 140%;
  font-size: 14px;
  color: #fff;
  border: 1px solid rgba(255,255,255,0.5);
  background-color: rgba(255,255,255,0.05);
  border-radius: 8px;
  box-sizing: border-box;
}
.field__text::-webkit-input-placeholder {
  color: #c88684;
}
.field__text::-moz-placeholder {
  color: #c88684;
}
.field__text:-moz-placeholder {
  color: #c88684;
}
.field__text:-ms-input-placeholder {
  color: #c88684;
}
.field__text:focus::-webkit-input-placeholder {
  color: transparent;
}
.field__text:focus::-moz-placeholder {
  color: transparent;
}
.field__text:focus:-moz-placeholder {
  color: transparent;
}
.field__text:focus:-ms-input-placeholder {
  color: transparent;
}
.step-list {
  margin-top: 60px;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
@media (max-width: 767px) {
  .step-list {
    margin-top: 50px;
  }
}
.step-list__item {
  width: calc((100% - 90px) / 4);
  position: relative;
}
@media (max-width: 1023px) {
  .step-list__item {
    margin-bottom: 40px;
    width: calc((100% - 40px) / 2);
  }
}
@media (max-width: 550px) {
  .step-list__item {
    width: 100%;
  }
}
.step-list__icn {
  margin-bottom: 16px;
  width: 40px;
}
.step-list__text {
  color: #000;
}
.step-list__text span {
  color: #961914;
  border-bottom: 1px solid #961914;
  cursor: pointer;
  transition: all 300ms ease;
}
.step-list__text span:hover {
  border-bottom: 1px solid transparent;
}
.step-list__title {
  margin-bottom: 8px;
  line-height: 130%;
  font-size: 18px;
  font-weight: 600;
}
.header {
  padding: 10px 0;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 3;
  transition: background 300ms 300ms ease, box-shadow 300ms 300ms ease;
}
.header .box-center {
  display: flex;
  align-items: center;
}
.header__logo {
  width: 80px;
  flex-shrink: 0;
}
@media (max-width: 767px) {
  .header__logo {
    margin: 10px 0 0;
    width: 60px;
  }
}
.header--show {
  background-color: #fff;
  box-shadow: 0 1px 5px 0 rgba(0,0,0,0.2);
  transition-delay: 0;
}
@media (max-width: 767px) {
  .header--show .nav-list {
    border-top: 2px solid #000;
    border-bottom: 2px solid #000;
  }
}
.header--show .nav-list--show {
  border: 0;
}
@media (max-width: 767px) {
  .header--show .nav-list:before,
  .header--show .nav-list:after {
    background-color: #000;
  }
}
.header--show .nav-list__link {
  color: #000;
}
.nav-list {
  width: 100%;
}
@media (max-width: 767px) {
  .nav-list {
    width: 26px;
    height: 20px;
    margin-top: -10px;
    position: absolute;
    top: 50%;
    right: 20px;
    border-top: 2px solid #fff;
    border-bottom: 2px solid #fff;
    cursor: pointer;
    transition: border-color 300ms 300ms ease;
  }
  .nav-list:before,
  .nav-list:after {
    content: '';
    width: 100%;
    height: 2px;
    margin-top: -1px;
    position: absolute;
    top: 50%;
    background-color: #fff;
    transition: background 300ms 300ms ease;
  }
}
.nav-list--show {
  border: 0;
}
@media (max-width: 767px) {
  .nav-list--show:before {
    transform: rotate(45deg);
  }
}
@media (max-width: 767px) {
  .nav-list--show:after {
    transform: rotate(-45deg);
  }
}
.nav-list--show .nav-list__box {
  right: 0;
  transition: right 300ms 300ms ease;
}
.nav-list__box {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  box-sizing: border-box;
}
@media (max-width: 767px) {
  .nav-list__box {
    max-width: 375px;
    padding-top: 80px;
    padding-bottom: 30px;
    flex-direction: column;
    justify-content: flex-start;
    position: fixed;
    top: 90px;
    right: -375px;
    bottom: 0;
    overflow-y: auto;
    background-color: #fff;
    transition: right 300ms ease;
  }
}
.nav-list__item {
  margin-left: 53px;
}
@media (max-width: 1023px) {
  .nav-list__item {
    margin-left: 25px;
  }
}
@media (max-width: 767px) {
  .nav-list__item {
    margin-left: 0;
    text-align: center;
  }
  .nav-list__item:first-child .nav-list__link {
    margin-top: 0;
  }
}
.nav-list__link {
  display: block;
  color: #fff;
  font-weight: 500;
  text-decoration: none;
  white-space: nowrap;
  border-bottom: 1px solid transparent;
  transition: color 300ms 300ms ease;
}
@media (max-width: 767px) {
  .nav-list__link {
    margin-top: 40px;
    font-size: 24px;
    color: #000;
  }
}
.nav-list__link:hover {
  border-bottom: 1px solid #961914;
}
